.ContactData {
	margin: 20px auto;
	width: 80%;
	text-align: center;
	box-shadow: 0 2px 3px #ccc;
	border: 1px solid #eee;
	padding: 10px;
	box-sizing: border-box;
}
.ContactData input {
	border: 1px solid #944317;
	padding: 5px;
	margin: 2px;
}
@media (min-width: 600px) {
	.ContactData {
		width: 500px;
	}
}
